.gallery__parent {
	display: flex;
	align-items: center;
	height: 40vw;
	max-height: 400px;
	justify-content: center;
	max-width: 100vw;
	overflow: hidden;
	position: relative;
	cursor: grab;
	width: calc(100vw - 10px);
	overflow: hidden;
	margin-top: 10px;

	& *:nth-child(n) {
		transition: opacity 100ms;
		pointer-events: none;
	}

	@media (max-height: 850px) {
		max-height: 350px;
		margin-top: 0px;
	}

}

.gallery__anim {
	& *:nth-child(n) {
		transition: all 250ms;
	}
}

.gallery__galleryItem {
	width: 70vw;
	max-width: 700px;
	max-height: 370px;
	height: 40vw;
	position: absolute;
	outline: none;

	overflow: hidden;
	display: block;

	img {
		border-radius: 15px;
	}

	@media (max-height: 850px) {
		max-height: 300px;
	}

	& *:nth-child(n) {
		height: 40vw;
		width: 100%;

		object-fit: cover;
		max-width: 700px;
		max-height: 370px;

		outline: none;

		@media (max-height: 850px) {
			max-height: 300px;
		}
	}
}

@media (max-width: 550px) {
	.gallery__parent {
		height: 211px;
	}
	.gallery__galleryItem {
		width: 85vw;
		height: fit-content;

		& *:nth-child(n) {
			height: 200px;
			object-fit: cover;
		}
	}
}